<template>
  <div class="limit">
    <div class="detail">
      <div class="left">
        <div class="header">
          <div class="avatar">
            <img :src="detail.coverImgUrl + '?param=200y200'" alt="" />
          </div>
          <div class="info">
            <h3 class="title">{{ detail.name }}</h3>
            <div class="user">
              <div class="creator">
                <img :src="creator.avatarUrl" alt="" /> <span>{{ creator.nickname }}</span>
              </div>
              <span>{{ detail.createTime | dateFormat('YYYY-MM-DD') }} 创建</span>
            </div>
            <div class="tags">
              <span>标签：</span>
              <span class="tags-item" v-for="(item, index) in detail.tags" :key="index"> {{ item }}</span>
            </div>
            <div class="desc">{{ detail.description }}</div>
          </div>
        </div>
        <artist-list class="asd" :songs="songs" />
      </div>
      <div class="right">
        <div class="like">
          <div class="title">喜欢这个歌单的人</div>
          <div class="content">
            <span v-for="item in subscribers" :key="item.id"><img :src="item.backgroundUrl" :title="item.nickname"/></span>
          </div>
        </div>
        <div class="relevant">
          <div class="title">相关推荐</div>
          <div class="content">
            <div class="item" v-for="item in playlists" :key="item.id">
              <img :src="item.coverImgUrl" alt="" />
              <div class=" ellipsis">
                <span class="name">{{ item.name }}</span>
                <div class="nikename">by. {{ item.creator.nickname }}</div>
              </div>
            </div>
          </div>
        </div>
        <div class="comments">
          <div class="title">精彩评论</div>
          <div class="content">
            <div class="item" v-for="item in comments" :key="item.commentId">
              <img :src="item.user.avatarUrl" alt="" />
              <div class="info">
                <div class="user">
                  <h2 class="name">{{ item.user.nickname }}</h2>
                  <span class="time"> · {{ utils.formTime(item.time) }}</span>
                </div>
                <div class="comment">
                  {{ item.content }}
                </div>
              </div>
            </div>
          </div>
          <el-pagination small layout="prev, pager, next" :total="total" @prev-click="pre" @next-click="next" @current-change="current" :current-page="cueerenPage"> </el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { playlistDetail, getSongDetail, SongsInfo, getSubscribers, getRelated, getComment } from '@/network/detail';
import ArtistList from '@/components/common/ArtistList/ArtistList';
export default {
  components: {
    ArtistList
  },
  data() {
    return {
      id: '',
      detail: [],
      creator: [],
      songs: [],
      // 歌单收藏者
      subscribers: [],
      // 相关推荐
      playlists: [],
      total: 0,
      limit: 8,
      comments: [],
      cueerenPage: 1
    };
  },
  created(){
      this.id = this.$route.query.id;
      this.getData();

  },
 
  methods: {
    getData() {
      playlistDetail(this.id).then((res) => {
        this.detail = res.data.playlist;
        this.creator = res.data.playlist.creator;
        let trackIds = res.data.playlist.trackIds;
        let newTrackIds = [];
        trackIds.forEach((item) => {
          newTrackIds.push(item.id);
        });
        newTrackIds = newTrackIds.join(',');
        getSongDetail(newTrackIds).then((res) => {
          console.log( res);
          res.data.songs.forEach((item) => {
            let songs = new SongsInfo(item);
            this.songs.push(songs);
          });
        });
      });
      // 获得歌单收藏者
      getSubscribers(this.id).then((res) => {
        this.subscribers = res.data.subscribers;
      });
      // 获得相关推荐
      getRelated(this.id, 5).then((res) => {
        this.playlists = res.data.playlists;
      });
      // 获得评论
      getComment(this.id, this.limit).then((res) => {
        this.total = res.data.total;
        this.comments = res.data.comments;
      });
    },
    // 下一页评论
    getNextComment(index) {
      getComment(this.id, this.limit, (index - 1) * this.limit).then(res=>{
        this.comments=res.data.comments
      });
    },

    pre(index) {
      this.cueerenPage = index;
      this.getNextComment(index);
    },
    next(index) {
      this.cueerenPage = index;
      this.getNextComment(index);
    },
    current(index) {
      this.cueerenPage = index;
      this.getNextComment(index);
    }
  }
};
</script>

<style scoped lang="less">
.detail {
  margin-top: 20px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  .left {
    width: 70%;
    padding: 15px;
    background-color: #fff;
    border-radius: 15px;
    
    .header {
      display: flex;
      .avatar {
        width: 200px;
        height: 200px;
        position: relative;
        img {
          position: relative;

          border-radius: 10px;
        }
      }
      .avatar::before {
        content: '';
        width: 95%;
        height: 95%;
        background: rgba(0, 0, 0, 0.2);
        transform: translate(15px, 15px);
        position: absolute;
        z-index: 0;
        border-radius: 8px;
      }
      .info {
        margin-left: 30px;
        .user {
          margin-top: 20px;
          display: flex;
          align-items: center;
          font-size: 14px;
          .creator {
            display: flex;
            align-items: center;
            img {
              width: 30px;
              height: 30px;
              border-radius: 50%;
            }
          }
          span {
            margin: 0 10px;
          }
        }
        .tags {
          margin-top: 15px;
          .tags-item {
            font-size: 14px;
            background-color: pink;
            margin: 0 5px;
            padding: 5px;
            border-radius: 10px;
          }
        }
        .desc {
          margin-top: 15px;
          font-size: 14px;
        }
      }
    }
    
  }
  .right {
    width: 28%;
    .title {
      font-size: 15px;
      padding: 5px 15px;
      font-weight: 600;
      border-left: 3px solid pink;
    }
    .like {
      width: 100%;
      background-color: #fff;
      border-radius: 10px;
      padding: 10px;
      margin-bottom: 50px;
      .content {
        padding: 5px 0;
        display: flex;
        flex-wrap: wrap;
        span {
          margin: 5px;
        }
        img {
          width: 35px;
          height: 35px;
          border-radius: 10px;
        }
      }
    }
    .relevant {
      width: 100%;
      background-color: #fff;
      border-radius: 10px;
      padding: 10px;
      margin-bottom: 50px;

      .content {
        padding: 5px 0;

        .item {
          display: flex;
          margin: 5px;
          img {
            width: 50px;
            height: 50px;
            margin-right: 10px;
            border-radius: 5px;
          }
          div {
            margin: 5px;
          }
          .name {
            font-size: 14px;
            font-weight: 600;
          }
          .nikename {
            padding-top: 5px;
            font-size: 8px;
            color: #ababab;
          }
        }
      }
    }
    .comments {
      width: 100%;
      background-color: #fff;
      border-radius: 10px;
      padding: 10px;
      .content {
        padding: 5px 0;
        .item {
          margin: 10px 5px;
          display: flex;
          img {
            width: 50px;
            height: 50px;
            border-radius: 50%;
          }
          .info {
            width: 100%;
            margin-left: 10px;
            .user {
              display: flex;
              align-items: center;
              .name {
                font-size: 14px;
              }
              .time {
                font-size: 12px;
                color: #a5a5c1;
              }
            }

            .comment {
              line-height: 16px;
              margin-top: 10px;
              font-size: 12px;
              background-color: #f5f5f5;
              color: 666;
            }
          }
        }
      }
    }
  }
}
</style>
