<template>
  <div id="artisr-list">
    <div v-if="songs">
      <div class="header">
        <span class="play el-icon-video-play" @click="allPlay"> 播放全部</span>
        <span class="collection">收藏</span>
      </div>
      <div class="artist-table">
        <ul class="title">
          <li class="th-index">序号</li>
          <li class="th-name">歌曲</li>
          <li class="th-artist">歌手</li>
          <li class="th-album">专辑</li>
          <li class="th-duration">时长</li>
        </ul>
        <div class="box">
          <ul v-for="(item, index) in songs" :key="item.id" class="list" :class="currenSong.id == item.id && playState ? 'active' : ''">
            <li>
              <div class="icon">
                <span class="num">{{ index + 1 }}</span>
                <i class="pause-btn el-icon-video-pause" @click="pauseState"></i>
                <i class="play-btn el-icon-video-play" @click="playSong(index)"></i>
              </div>
              <div class="song ellipsis">
                <img :src="item.songImg" alt="" />
                {{ item.songname }}
              </div>
              <div class="singer ellipsis ">
                {{ item.singer }}
              </div>
              <div class="ablum ellipsis">{{ item.blum }}</div>
              <div class="time">
                {{ item.time | dateFormat('MM:SS') }}
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div v-else>哈哈哈</div>
  </div>
</template>

<script>
import { mapActions, mapGetters, mapMutations } from 'vuex'
export default {
  props: ['songs'],
  computed: {
    ...mapGetters(['playState', 'currenSong'])
  },
  methods: {
    ...mapActions(['playAll', 'selectPlay']),
    ...mapMutations({
      setPlayState: 'SET_PLAYSTATE',
      setPauseState: 'SET_PAUSE_STATE'
    }),
    allPlay() {
      this.playAll(this.songs)
    },
    playSong(index) {
      this.selectPlay({
        list: this.songs,
        index
      })
    },
    pauseState() {
      this.setPauseState(false)
    }
  }
}
</script>

<style scoped lang="less">
#artisr-list {
  width: 100%;
}
.header {
  float: right;
  margin: 15px 0;
  .play {
    background-color: pink;
    border-radius: 10px;
  }
  .collection,
  .play {
    margin: 0 8px;
    padding: 5px;
  }
}
.artist-table {
  width: 100%;
  font-size: 14px;
  .title {
    line-height: 50px;
    width: 100%;
    padding: 0 9px;
    display: flex;
    justify-content: space-between;
    font-weight: 300;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .th-index {
    width: 5%;
    // text-align: center;
  }
  .th-name {
    flex: 1;
    padding-left: 20px;
   
  }
  .th-artist {
    width: 20%;
  }
  .th-album {
    width: 20%;
  }
  .th-duration {
    // text-align: right;
    padding-right: 40px;
  }
  //  .title{
  //     background-color: #fafafa;
  //     display: flex;
  //   }
  tr:nth-child(2n) {
    background-color: #fafafa;
  }


  .list {
    width: 100%;

    line-height: 50px;
    background-color: #fff;
    color: #999;

    li {
      width: 100%;
      color: #000;
      padding: 0 9px;
      display: flex;
      justify-content: space-between;
    }
    .icon {
      width: 5%;
      display: flex;
      align-items: center;
      padding-left: 10px;
      .pause-btn,
      .play-btn {
        color: #fa2800;
        font-size: 20px;
        display: none;
        cursor: pointer;
      }
    }

    .song {
    //  width: 30%;
      display: flex;
      align-items: center;
      flex: 1;
    padding-left: 20px;
      img {
        width: 35px;
        height: 35px;
        border-radius: 10px;
        margin-right: 8px;
      }
    }
    .singer {
      width: 20%;
     
      
    }
    .time {
      padding-right: 40px;
     
    }
    .ablum {
       width: 20%;
    
    }
  }
    .list:hover {
    background-color: #e8e9ed;
    .icon {
      .play-btn {
        display: block;
      }
      .pause-btn {
        display: none;
      }
      .num {
        display: none;
      }
    }
  }
  .active:hover {
    .icon {
      .pause-btn {
        display: block;
      }

      .num {
        display: none;
      }
    }
  }
  .active {
    .num,
    .song,
    .singer,
    .ablum {
      color: pink !important;
    }
    .icon {
      .play-btn {
        display: none!important;
      }
      .num {
        display: block;
      }
    }
  }
}
</style>
